class FundHeroSection extends HTMLElement {
  constructor() {
    super();
  }

  connectedCallback() {
    this.innerHTML = `
    <section class="fund-hero hero-full-section hero-full-section--fund">

   <img loading="lazy"  class="hero-full-section__decor hero-full-section__decor--fund" src="https://lukasemperfi.github.io/space-lab/assets/images/fund-hero/decor-right-desktop.png" alt="hero-section-bg" >
   <img loading="lazy"  class="hero-full-section__decor-mobile " src="https://lukasemperfi.github.io/space-lab/assets/images/fund-hero/decor-right-mobile.png" alt="hero-section-bg" >

      <div class="hero-full-section__video-container-fund">
        <video 
            class="hero-full-section__video-fund"   
            muted 
            loop 
            playsinline
           autoplay
            aria-label="Finity Touch promotional video"
            poster="https://lukasemperfi.github.io/space-lab/assets/video/fund-poster.webp"
       
            preload="metadata"
          >
            <source src="https://lukasemperfi.github.io/space-lab/assets/video/fund-hero.mp4" type="video/mp4">
            Your browser does not support the video tag.
          </video>
      </div>
          <div class="hero-full-section__content">
            <div class="hero-banner hero-banner--size-md">
                <h1 class="hero-banner__title ">
                    <span class="hero-banner__title-line1 hero-banner__title-line1--accent-4">Finity</span>
                    <span class="hero-banner__title-line2 hero-banner__title-line2--accent-4">Fund</span>
                </h1>
                    <p class="hero-banner__description">
The Finity Foundation is a fund that fuels the development of new active projects, as well as the possibility of discovering new fragments of the ecosystem.
                    </p>
                <a href="#" class="hero-banner__button wavy-button-2 color-accent-4">Leave a request</a>
                </div>
            </div>
     
    </section>
    `;
  }
}

customElements.define('fundhero-section', FundHeroSection);
